div {
  box-sizing: border-box;
}
.black-nav {
  background-color: black;
  color: white;
  padding: 0.6rem;
}

.list {
  text-align: left;
  padding-left: 20px;
  border-bottom: 1px solid grey;
}

.modal {
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 20px;
  background: #eee;
  text-align: left;
}

.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: black;
  color: white;
  padding: 0.6rem;
}

.footer p {
  margin: 0;
}
